
import { defineComponent } from "vue";

interface Data {
  product: {
    name: string;
    description: string;
    price: number;
    stock: number;
    sold: number;
  };
  errors: string[];
}

export default defineComponent({
  name: "Criar Produto",
  data(): Data {
    return {
      product: {
        name: "",
        description: "",
        price: 0,
        stock: 0,
        sold: 0
      },
      errors: []
    };
  }
});
